import React,{useEffect,useContext,useState,useRef} from 'react';
import {NavLink,useHistory} from 'react-router-dom';
import {MyContext} from '../config/Context';
import {Spinner} from 'reactstrap';


// Components
import Bread from '../components/Bread';
import { storage } from 'firebase';
import Helper  from '../config/Helper';
import { countBy } from 'lodash';

const Profile = () => {

  const [error,setError] = useState();
  const [success,setSuccess] = useState();
  const [state,setState] = useState({file:null});

  //Context  
   const { auth,updateprofile,delUser,userLogout,upload } = useContext(MyContext);

  let history = useHistory();
  const [loading,setLoading] = useState(false);


  //Ref
   const photo = useRef(null); 


 async function Handle(event){
       
        event.preventDefault();  
        let profile = {
            nick_name  :  event.target.nick_name.value,
            profession  :  event.target.profession.value,
            mobile  :  event.target.mobile.value,
            birthday  :  event.target.birthday.value,
            gender  :  event.target.gender.value,
            bio  :  event.target.bio.value,
        };

        if(event.target.photo.files.length > 0){
            
            
            let file = event.target.photo.files[0];
            file = await upload(file);
            profile.photo = file; 
            updateprofile(profile);  
            
            photo.current.value = "";

        }else{    
           
            updateprofile(profile);

        }

  }
    
    function logout(){
       
        userLogout();

     }

  useEffect( () => {

  });
  return (<>
             { auth == null ?  history.push("/"): 

            <>
            <Bread title="My Profile" des="" />
              <section className="profile py-5">
                  <div className="profile-container  pt-4 bootstrap snippet">
                  <div className="row">
                  <div className="col-md-4 col-lg-4">
                    <div className="text-center mb-4">
                      {auth.profile.photo != null ? <img style={{height:'350px'}} src={auth.profile.photo} className="profile-image w-100 avatar img-circle img-thumbnail" /> : <img src="http://ssl.gstatic.com/accounts/ui/avatar_2x.png" className="profile-image w-100 avatar img-circle img-thumbnail" /> }
                    </div>        
                      <ul className="list-group">
                        <li className="list-group-item text-muted text-center ">{auth.profile.nick_name}</li>
                        <li className="list-group-item ">
                          <span className="d-block float-left"><strong>Gender</strong></span>
                          <span className="d-block float-right">{auth.profile.gender}</span> 
                        </li>
                        <li className="list-group-item ">
                          <span className="d-block float-left"><strong>Birthday</strong></span>
                            <span className="d-block float-right">{auth.profile.birthday}</span> 
                        </li>
                        <li className="list-group-item ">
                          <span className="d-block float-left"><strong>Phone</strong></span>
                            <span className="d-block float-right">{auth.profile.mobile}</span> 
                        </li>
                      </ul>   
                    </div>
                    <div className="col-md-8 col-lg-8 py-2">
                    <form onSubmit={Handle}   className="php-email-form">
                            <ul className="nav nav-tabs" id="myTab" role="tablist">
                              <li className="nav-item">
                                <a className="nav-link active" id="about-tab" data-toggle="tab" href="#about" role="tab" aria-controls="about" aria-selected="true">About</a>
                              </li>
                              <li className="nav-item">
                                <a className="nav-link" onClick={logout} data-toggle="tab" role="tab" href="" >Logout</a>
                              </li>
                            </ul>
                                <div className="tab-content " id="myTabContent">
                                  <div className="tab-pane fade show active" id="about" role="tabpanel" aria-labelledby="about-tab">
                                    <div className="py-3" >
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                            <label>Username</label>
                                            <input readOnly defaultValue={auth.profile.username}  type="text" className="form-control"   placeholder="Username" />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                            <label>Email</label>
                                            <input readOnly defaultValue={auth.profile.email}  type="text" className="form-control"   placeholder="Your Email" />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                            <label>Name</label>
                                            <input defaultValue={auth.profile.nick_name} name="nick_name" type="text" className="form-control"   placeholder="Name" />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                            <label>Profession</label>
                                            <input defaultValue={auth.profile.profession} name="profession" type="text" className="form-control"   placeholder="Profession" />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                          <label>Phone</label>
                                            <input defaultValue={auth.profile.mobile} name="mobile" type="text" className="form-control"   placeholder="Phone" />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                          <label>Birthday</label>
                                            <input defaultValue={auth.profile.birthday} name="birthday" type="date" className="form-control"   />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                          <label>Gender</label>
                                            <select defaultValue={auth.profile.gender} name="gender" className="form-control" >
                                              <option  value="male" >Male</option>
                                              <option  value="female" >Female</option>
                                            </select>
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                          <label>File</label>
                                            <input multiple ref={photo} name="photo" type="file" className="form-control" />
                                          </div>
                                        </div>
                                        <div className="form-row">
                                          <div className="col-md-12 form-group">
                                            <label>Bio</label>
                                            <textarea defaultValue={auth.profile.bio} name="bio" className="form-control" ></textarea>
                                          </div>
                                        </div> 
                                      </div>
                                  </div>
                                  </div>
                                  <div className="text-center">
                                          <button className="mybtn2 active" type="submit">Update</button>
                                   </div>
                                   </form>
                                  </div>
                            </div>          
                       </div>
                  </section>
              </>
          }
 </>)}
export default Profile;